'use strict';

/**
* Generate pseudorandom numbers drawn from a Weibull distribution.
*
* @module @stdlib/random-weibull
*
* @example
* import weibull from '@stdlib/random-weibull';
*
* var arr = weibull( [ 10 ], 2.0, 5.0 );
* // returns <ndarray>
*
* @example
* import weibull from '@stdlib/random-weibull';
*
* var arr = weibull( [ 10 ], 2.0, 5.0, {
*     'dtype': 'generic'
* });
* // returns <ndarray>
*
* @example
* import weibull from '@stdlib/random-weibull';
*
* var rand = weibull.factory();
* // returns <Function>
*
* var arr = rand( [ 10 ], 2.0, 5.0 );
* // returns <ndarray>
*
* @example
* import weibull from '@stdlib/random-weibull';
*
* var rand = weibull.factory();
* // returns <Function>
*
* var arr = rand( [ 10 ], 2.0, 5.0, {
*     'dtype': 'generic'
* });
* // returns <ndarray>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;

// exports: { "assign": "main.assign", "factory": "main.factory" }
